import axios from '@/api/axios'

const checkId = async (data, result) => {
  const options = {
    method: 'POST',
    url: '/user/id',
    data: data
  }
  await axios(options)
    .then((response) => {
      if(response.data.success) {
        result(response.data)
      }
    })
    .catch((response) => {
      alert(response.response.data.error.message)
    })
}

